@import 'base/_fonts';
@import 'base/_variables';

html {
    height: 100%;
 }    // scroll-behavior: smooth

body {
    // display: flex
    // flex-direction: column
    // margin-top: auto
    height: 100%;
    // height: 100%
    width: 100%;
    // background: $background-main-color
    overflow-x: hidden;
    position: relative;
    font-family: 'Museo Sans', sans-serif;
    font-weight: 500;
    letter-spacing: 0.025em; }
.fa-search {
    font-size: 25px; }
#root {
    height: 100%; }
.App {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    transition: all 0.5s ease;
    &_homepage {
        transition: all 0.5s ease;
        background: $background-main-color; }
    &_searchpage {
        transition: all 0.5s ease;
        background: $grey; } }


* {
    box-sizing: border-box; }
img {
    align-self: start; }
.container {
    width: 1080px;
    margin: 0 auto;
    position: relative;
    // padding-left: 15px
 }    // padding-right: 15px
.flex {
    display: flex; }

.justify-space-between {
    justify-content: space-between; }

* {

    margin: 0;
    padding: 0; }
a {
    text-decoration: none;
    color: white; }
.link {
    color: $main-color;
    font-family: Roboto;
    font-weight: 400; }

p {
    margin: 0;
    padding: 0; }
input {
    box-sizing: border-box; }


ul, li {
	display: block;
	padding: 0;
	margin: 0;
	box-sizing: border-box; }

.logo {
    font-size: 26px;
    font-weight: 900;
    // color: white

    &__link {
        // display: flex
        align-items: center;
        color: inherit; }
    &__icon {
        color: white;
        width: 40px;
        height: 40px;
        background: $green-gradient;
        border-radius: 5px;
        text-align: center;
        line-height: 1.6;
        margin-right: 10px;
 } }        // box-shadow: 0 6px 9px rgba(0,0,0,0.2)

