@media screen and (max-width: 1200px) {
    .container {
        width: 850px; }
    .search-page {
        // background: blue
        &__booklist {
            // grid-gap: 10px
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); } } }




@media screen and (max-width: 991px) {
    .container {
        // background: red
        width: 750px;
        height: 100%; }
    .search-page {
        &__booklist {
            grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
            display: grid;
            grid-gap: 15px;
            // grid-template-columns: repeat(auto-fill, minmax(50px, 100px))
            // div
 } }            //     width: 50px
    // .book-item
    //     width: 50%
    //     &__block
 }    //         width: 20px


@media (max-width: 768px) {
    .logo {
        margin: 20px 0 50px; }
    .header {
        &__menu {
            width: 100%; }
        &__block {
            flex-direction: column; } }

    .offer {

        &__block {
            height: 700px !important;
            width: 100%;
            background-size: 100% !important; } }
    .search-field {
        width: 100%;
        align-items: center;
        &__title {
            font-size: 40px;
            padding-top: 220px; }
        &__form {
            width: 100%; }
        &__input {
            width: 100%;
            &_top {
                height: 50px;
 } } }                // width: 100%
    .button {
        &_top {
            top: 7px; } }


    .container {
        // background: green
        width: 575px; }
    .search-page {
        padding-top: 20px;
        &__booklist {
            grid-template-columns: repeat(auto-fill, minmax(2fr, 1fr)); } }
    .book-item-popup {
        &__info {
            width: 100%; }
        &__window {
            &-block {
                flex-direction: column; } } } }


@media only screen and (max-width: 575px) {

    .book-item-popup {
        &__image {
            width: 100px; }
        &__info {
            width: 100%; }
        &__window {
            position: fixed;
            width: 100px;
            top: 0;

            &-block {
                flex-direction: column; } } }
    .container {
        padding: 15px;
        width: 100%; }
    .search-page {
        // background: blue
        &__booklist {
            grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); } }
    .book-item {
        &__block {
            height: 350px; }
        &__image {
            height: 350px; } } }



@media only screen and (max-width: 480px) {
    .footer {
        &__block {
            // padding: 15px

            width: 100%; } }
    .container {
        padding: 0 15px;
        width: 100%;
 }        // background: white
    .search-page {
        padding-bottom: 230px;
        &__booklist {
            grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); } }
    .book-item {
        &__block {
            height: 270px; }
        &__image {
            height: 270px; } }
    .offer {
        padding-bottom: 250px;
        &__block {
            height: 600px !important; } }
    .search-field {
        &__input {
            height: 60px;
            &_top {
                height: 50px; } }
        &__title {
            font-size: 33px;
            padding-top: 190px; } }
    .button {
        &_offer {
            height: 60px;
            padding-top: 18px; }
        &_top {
            top: 0;
            padding: 0;
            height: 100%; } }

    .footer {
        &__block {
            flex-direction: column;
            padding: 20px 0 40px;
            height: 230px; } } }


